<template>
  <div class="card">
    <div class="card-header">Exam07ComputedProperties</div>
    <div class="card-body">
      <p>ssn1: {{ name }}</p>
      <p>ssn1: {{ ssn1 }}</p>
      <p>ssn2: {{ ssn2 }}</p>
      <p>ssn: {{ ssn1 }}-{{ ssn2.charAt(0) + "******" }}</p>
      <p>ssn: {{ getSsn() }}</p>
      <p>ssn: {{computedSsn}}</p>
      <hr />
      <button class="btn btn-info btn-sm mr-2" @click="changeName">name 변경</button>
      <button class="btn btn-info btn-sm mr-2" @click="changeSsn2">ssn2 변경</button>
    </div>
  </div>
</template>


<script setup>
import { computed, ref } from "vue";

//반응형 속성 선언
const name = ref("홍길동");
const ssn1 = ref("880515");
const ssn2 = ref("1110345");

//해당사항 변경시에만 실행되는 computed 메소드
const computedSsn = computed(() => {
  console.log("computedSsn() 실행");
  const ssn = ssn1.value + "-" + ssn2.value.charAt(0) + "*******";
  return ssn;
});

//메소드 선언
//모든 반응형 속성이 변경될 때마다 실행됨
function getSsn() {
  console.log("getSsn() 실행");
  const ssn = ssn1.value + "-" + ssn2.value.charAt(0) + "*******";

  return ssn;
}

//이름 변경될 때 상단 메소드 실행되는지 확인해보기
function changeName() {
  name.value += "동";
}

//ssn 변경시에만 실행되는지 확인
function changeSsn2(){
    ssn2.value += "2";
}
</script>
