<template>
  <div class="card" :id="$attrs.id">
    <div class="card-header">
        <span :class="$attrs.class" :style="attrs.style">FallthroughAttributes </span>
    </div>
    <div class="card-body">
      <router-link :to="attrs.to" class="btn btn-info btn-sm mr-2">이동</router-link>
      <button @click="attrs.onClick" class="btn btn-info btn-sm">버튼</button>
      <hr/>
      <button @click="handleBtn1" class="btn btn-info btn-sm mr-2">버튼</button>
      <button @click="handleBtn2" class="btn btn-info btn-sm">버튼</button>
    </div>
  </div>
</template>

<script>
  //해당 컴포넌트 루트 상속시 내부 요소에 루트 class, id, to 등 반영 안함
  export default{
    inheritAttrs: false,
  };
</script>

<script setup>
  //상속된 루트 class, id, to 등 정보 받음
  import {useAttrs} from "vue";//루트에 있는 class와 id등 저장하기 위함
  import {useRouter} from "vue-router";//루트 라우터 정보 js에서 push 하기 위함
  const attrs = useAttrs();
  const router = useRouter();

  //js로 실행하는 방식
  function handleBtn1(){
    router.push(attrs.to);
  }
  function handleBtn2(){
    attrs.onClick();
  }
</script>

<style scoped></style>
